<script>
import MobileMockup from '@/components/MobileMockup'
import IntroPage from './comps/IntroPage'
import IntroTextPage from './comps/IntroTextPage'
import MessageTipPage from './comps/MessageTipPage'
import WeiboPage from './comps/WeiboPage'
import WechatPage from './comps/WechatPage'
import TwitterPage from './comps/TwitterPage'
import TelegramPage0 from './comps/TelegramPage0'
import TelegramPage from './comps/TelegramPage'
import TelegramPage2 from './comps/TelegramPage2'
import YouWish from './comps/YouWish'
// import { enableBodyScroll } from 'body-scroll-lock'

const ScrollMagic = window.ScrollMagic

export default {
  name: 'HomeView',
  components: {
    MobileMockup,
    IntroPage,
    IntroTextPage,
    MessageTipPage,
    WeiboPage,
    WechatPage,
    TwitterPage,
    TelegramPage0,
    TelegramPage,
    TelegramPage2,
    YouWish
  },
  data () {
    return {
      sections: {
        section0: {
          isActive: false
        },
        section1: {
          isActive: false
        },
        section2: {
          isActive: false
        },
        // section3: {
        //   isActive: false
        // },
        section4: {
          isActive: false
        },
        section5: {
          isActive: false
        },
        section6: {
          isActive: false
        },
        section7: {
          isActive: false
        },
        section8: {
          isActive: false
        },
        section9: {
          isActive: false
        },
        section12: {
          isActive: false
        },
        section10: {
          isActive: false
        }
      }
    }
  },
  computed: {
    isShowLogo () {
      const activeIndex = Object.keys(this.sections)
        .findIndex(key => this.sections[key].isActive)
      return activeIndex < 3
    }
  },
  methods: {
    initScrollMagic () {
      const controller = new ScrollMagic.Controller()

      Object.keys(this.sections).forEach(sectionName => {
        new ScrollMagic.Scene({
          triggerElement: this.$refs[sectionName],
          triggerHook: 0.5,
          duration: '100%'
        })
          .on('enter', () => {
            this.sections[sectionName].isActive = true
            // if (['section4', 'section8', 'section10'].includes(sectionName)) {
            //   enableBodyScroll(document.body)
            // }
          })
          .on('leave', () => {
            this.sections[sectionName].isActive = false
          })
          // .addIndicators()
          .addTo(controller)
      })
    }
  },
  mounted () {
    this.initScrollMagic()

    if (location.hash === '#section11') {
      this.$refs.section11.scrollIntoView()
    }
  }
}
</script>

