<template lang="pug">
div.Telegram-page(ref="TelegramPage")
  Telegram-chat-item.animate__animated.animate__fadeInUp(
    v-for="(item, index) in messages",
    :key="index",
    :name="item.name",
    :name-color="item.nameColor",
    :time="item.time",
    :reply="item.reply",
    :type="item.type",
    :text-content="item.text",
    :srcs="item.srcs"
  )
</template>

<script>
import TelegramChatItem from '@/components/TelegramChatItem'
// import sleep from '@/utils/sleep'

const PerfectScrollbar = window.PerfectScrollbar

const messages = [
  {
    name: '长沙求情侣',
    nameColor: '#20FFBC',
    type: 'text',
    text: '有谁分享自己老婆女友给我看著撸的？',
    time: '4:48 PM'
  },
  {
    name: '开坦克的',
    nameColor: '#F59033',
    type: 'text',
    text: '谁要看我女友照片？',
    time: '4:52 PM'
  },
  {
    name: '开坦克的',
    nameColor: '#F59033',
    type: 'text',
    text: '有人射屏吗？',
    time: '5:00 PM'
  },
  {
    name: '开坦克的',
    nameColor: '#F59033',
    type: 'text',
    text: '喜欢毛多水也多的，舒服啊',
    time: '5:30 PM'
  }
]

export default {
  name: 'TelegramPage',
  components: {
    TelegramChatItem
  },
  data () {
    return {
      isActive: false,
      messages: [],
      index: 0,
      timers: []
    }
  },
  methods: {
    push () {
      this.messages.push(messages[this.index])
      if (this.isActive) {
        this.$refs.TelegramPage.scrollTop = this.$refs.TelegramPage.scrollHeight
      }
      this.timers[this.index] = setTimeout(() => {
        if (this.index < messages.length - 1) {
          this.index++
          this.push()
        } else {
          setTimeout(() => {
            window.scrollTo(0, window.innerHeight)
          }, 2000)
        }
      }, 1000)
    }
  },
  mounted () {
    this.isActive = true
    this.push()

    const ps = new PerfectScrollbar(this.$refs.TelegramPage, {
      suppressScrollX: true,
      useBothWheelAxes: true
    })
    ps.update()
  },
  beforeDestroy () {
    this.isActive = false
    this.messages = []
    this.timers.forEach(item => {
      clearTimeout(item)
    })
  }
}
</script>

<style scoped lang="scss">
.Telegram-page {
  height: 100%;
  overflow: hidden;
  color: #FFF;
}
</style>
