<template lang="pug">
div.wechat-chat-item(:class="role")
  div.content(:class="role")
    //- Text
    div.text(v-if="type === 'text' && role === 'sender'")
      p {{ textContent }}
    div.text(v-if="type === 'text' && role === 'receiver'")
      p {{ textContent }}
    //- Image
    div.image(v-if="type === 'image'")
      img(:src="src")
</template>

<script>
export default {
  name: 'WechatChatItem',
  props: {
    // text | image
    type: {
      type: String,
      default: 'text'
    },
    role: {
      type: String,
      default: 'sender'
    },
    textContent: {
      type: String,
      default: 'no content'
    },
    src: {
      type: String,
      default: ''
    }
  }
}
</script>

