<template lang="pug">
div.message-tip-page
  span(ref="content")
  //- p(v-html="content")
</template>

<script>
import Typed from 'typed.js'
export default {
  name: 'MessageTipPage',
  props: {
    content: {
      type: String,
      default: ''
    }
  },
  methods: {
    typed () {
      this.typed = new Typed(this.$refs.content, {
        strings: [this.content],
        smartBackspace: true,
        backDelay: 2000,
        startDelay: 0,
        typeSpeed: 50,
        showCursor: true,
        cursorChar: '_',
        autoInsertCss: true
      })
    }
  },
  mounted () {
    this.typed()
  }
}
</script>

<style scoped lang="scss">
.message-tip-page {
  color: #FFF;
  max-width: 80vw;
  font-size: 20px;
  line-height: 1.5;
}
</style>
