<template lang="pug">
div.wechat-page(ref="wechatPage")
  wechat-chat-item.animate__animated.animate__fadeInUp(
    v-for="(item, index) in messages",
    :key="index",
    :type="item.type",
    :role="item.role",
    :text-content="item.text",
    :src="item.src"
  )
</template>

<script>
import WechatChatItem from '@/components/WechatChatItem'
import sleep from '@/utils/sleep'
import { clearAllBodyScrollLocks } from 'body-scroll-lock'

const messages = [
  {
    type: 'text',
    role: 'sender',
    text: '是你干的吗？'
  },
  {
    type: 'text',
    role: 'receiver',
    text: '是...'
  },
  {
    type: 'text',
    role: 'receiver',
    text: '对不起，对不起...'
  },
  {
    type: 'text',
    role: 'receiver',
    text: '求你原谅我!'
  }
]

export default {
  name: 'WechatPage',
  components: {
    WechatChatItem
  },
  props: {
    isActive: {
      type: Boolean,
      default: false
    },
    msg: String
  },
  data () {
    return {
      messages: []
    }
  },
  mounted () {
    messages.forEach(async (message, index) => {
      await sleep(1000 * (index + 1))
      this.messages.push(message)
      if (index === messages.length - 1) {
        clearAllBodyScrollLocks()
      }
    })
  },
  beforeDestroy () {
    this.messages = []
    clearAllBodyScrollLocks()
  }
}
</script>

<style scoped lang="scss">
.wechat-page {
  color: #FFF;
}
</style>
